import { GET_DATA, GET_NOTIFICATIONS, NOTIFICATIONS_LOADING } from './types'

import { NotificationManager } from 'react-notifications'
import axios from 'axios'

export const getData = () => async (dispatch) => {
	try {
		const res = await axios.get(`${process.env.REACT_APP_API_URL}/data/`)

		dispatch({
			type: GET_DATA,
			payload: res.data,
		})
	} catch (err) {
		NotificationManager.error(
			'Wystpi bd przy wczytywaniu strony, sprbuj odwiey stron',
			'Bd',
			10 ** 6
		)
	}
}

export const getNotifications = () => async (dispatch) => {
	dispatch({ type: NOTIFICATIONS_LOADING })

	try {
		const res = await axios.get(
			`${process.env.REACT_APP_API_URL}/notifications/`
		)

		dispatch({
			type: GET_NOTIFICATIONS,
			payload: res.data,
		})
	} catch (err) {
		NotificationManager.error(
			'Nie udao si zaadowa powiadomie',
			'Bd',
			10 ** 6
		)
	}
}
